.App {
  text-align: center; 
  position: relative; 
  width: 1100px;
  height: auto;
  margin: 0 auto;
}

.container{
  margin: 0 auto;
  display: flex;
  align-items: center;    
}

#header{
  width: 280px;
  height: 100vh;  
}

#about{
  width: 750px;
  height: 100vh;
  margin: 0 auto;  
}

#about p{
  text-align: justify;
  margin: 0px;
}

#projects{
  width: 750px;
  height: 150vh;  
}

.page-header{
  box-sizing: content-box;
  width: 100%;    
}

.page-header h1, h3{
  margin: 0px;  
}

#personal-links{
  margin: 10px auto;
}

.section{
  margin: 0 auto;
}

.section-title{
  border-top: solid 1px tomato;
  border-bottom: solid 1px tomato;
  padding: 10px;  
  text-align: center;
}



